// SCREEN SIZES
$screen-sm: 576px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1200px;
$screen-xxl: 1300px;
$screen-full: 1400px;




// BACK GROUNDS COLORS
// Signature Style Colors
$bgPrimaryColor: #193fb4; // app body
$bgPrimaryLightColor: lighten($bgPrimaryColor, 25%); // elements (some buttons, borders, lines etc)

// Secondary Colors
$bgSecondaryColor: #ecf1f5; // main section
$bgBlockColor: #ffffff; // blocks 
$bgMessageColor: darken($bgSecondaryColor, 5%); // messages bgc (inputs, textareas, dialogs messages)




// TEXTS COLORS
$textPrimaryColor: #545454; // main
$textSecondaryColor: #888888; // description

$textDarkColor: #353535;
$textLightColor: #eeedf8;



// ________Other Colors
$warningColor: #b3465c;





// ________Text Extends
%title-text {
    font-size: 15px;
    font-weight: 600;
    color: $textPrimaryColor;
}

%descr-text {
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    color: $textSecondaryColor;
}




// ________Buttons Extends
%button {
    color: $bgBlockColor;
    background-color: $bgPrimaryLightColor;
    &:hover {
        opacity: 0.8;
    }
}

// opposite button
%opp-button {
    border: 1px solid $bgPrimaryLightColor;
    color: $bgPrimaryLightColor;
    background-color: $bgBlockColor;
}



// ________Blocks Extends
%block {
    box-shadow: 0px 0px 4px 0px rgba(50, 50, 50, 0.2);
    background-color: $bgBlockColor;
}
